import React from "react";
import { Container } from "react-bootstrap";
import { Slide } from "react-slideshow-image";
import 'react-slideshow-image/dist/styles.css'
import electronics from '../assets/electronics.png'
import fashion from '../assets/fashion.png'
import office from '../assets/office.png'
import health from '../assets/health.png'
import phones from '../assets/phones.png'
import computing from '../assets/computing.png'
import groceries from '../assets/groceries.png'
import accessories from '../assets/accessories.jpg'

const CategoriesSlider = () => {
  const style = {
    textAlign: 'center',
    background: '#ffffff',
    padding: '20px 0',
      fontSize: '30px'
  };

  const properties = {
    duration: 1000,
    slidesToShow: 4,
    slidesToScroll: 2,
    autoplay: false,
    indicators: true,
    responsive: [
        {
            breakpoint: 1024,
            settings: {
                slidesToShow: 6,
                slidesToScroll: 2
            }
        },
        {
            breakpoint: 500,
            settings: {
                slidesToShow: 4,
                slidesToScroll: 1
            }
        }
    ]
  };

  return (
    <Container className="rounded">
      <div>
        <Slide {...properties}>
          <div style={style}><img className="d-flex w-50" src={electronics}/></div>
          <div style={style}><img className="d-flex w-50" src={fashion}/></div>
          <div style={style}><img className="d-flex w-50" src={office}/></div>
          <div style={style}><img className="d-flex w-50" src={health}/></div>
          <div style={style}><img className="d-flex w-50" src={accessories}/></div>
          <div style={style}><img className="d-flex w-50" src={phones}/></div>
          <div style={style}><img className="d-flex w-50" src={computing}/></div>
          <div style={style}><img className="d-flex w-50" src={groceries}/></div>
        </Slide>
      </div>
    </Container>
  );
};

export default CategoriesSlider;