import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './MyStyle.css'  
import { Container, Card, CardGroup, Col, Button } from 'react-bootstrap'; 
import cad1 from '../assets/cad1.jpg'; 
import cad3 from '../assets/cad3.jpg'; 
import cad2 from '../assets/cad2.jpg'; 
import cad4 from '../assets/cad4.jpg'; 
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faHeart } from '@fortawesome/free-regular-svg-icons'
function MyCard() {  
  return (  
    <div className="App">  
   <Container className='container'>  
        <Col> 
  <CardGroup style={{'width':"auto"}}>        
  <Card className='mx-3 rounded'>  
  <Card.Img className='rounded' variant="top" src={cad1} />  
  <Card.Body>  
    <Card.Title>Redmi Pro</Card.Title>  
              <Card.Subtitle className="mb-3 text-muted">$45,000</Card.Subtitle>
              <Button className='btn btn-sm btn-warning'>Add To Cart</Button><FontAwesomeIcon className='mx-3 btn btn-sm btn-warning' icon={faHeart} /> 
              </Card.Body> 
            </Card> 
<Card className='mx-3 rounded'>  
  <Card.Img className='rounded' variant="top" src={cad2} />  
  <Card.Body>  
    <Card.Title>Tecno Lite</Card.Title>  
              <Card.Subtitle className="mb-3 text-muted">$45,000</Card.Subtitle>
              <Button className='btn btn-sm btn-warning'>Add To Cart</Button><FontAwesomeIcon className='mx-3 btn btn-sm btn-warning' icon={faHeart} /> 
              </Card.Body> 
            </Card>  
<Card className='mx-3 rounded'>  
  <Card.Img className='rounded' variant="top" src={cad3} />  
  <Card.Body>  
    <Card.Title>Tecno Pro</Card.Title>  
              <Card.Subtitle className="mb-3 text-muted">$45,000</Card.Subtitle>
              <Button className='btn btn-sm btn-warning'>Add To Cart</Button><FontAwesomeIcon className='mx-3 btn btn-sm btn-warning' icon={faHeart} /> 
              </Card.Body> 
            </Card>
<Card className='mx-3 rounded'>  
    <Card.Img className='rounded' variant="top" src={cad4} />  
  <Card.Body>  
    <Card.Title>Infinix Lite</Card.Title>  
              <Card.Subtitle className="mb-3 text-muted">$45,000</Card.Subtitle>
              <Button className='btn btn-sm btn-warning'>Add To Cart</Button><FontAwesomeIcon className='mx-3 btn btn-sm btn-warning' icon={faHeart} /> 
              </Card.Body> 
            </Card> 
  </CardGroup>            
    </Col>  
      </Container>  
      <br/>
    </div>  
  );  
}  
export default MyCard;  