import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './MyStyle.css'  
import { Container, Card, CardGroup, Col, Button } from 'react-bootstrap'; 
import airpod from '../assets/airpod.jpg'; 
import smart from '../assets/smart.jpg'; 
import bluetooth from '../assets/bluetooth.jpg'; 
import charger from '../assets/charger.jpg'; 
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faHeart } from '@fortawesome/free-regular-svg-icons'
function Products() {  
  return (  
    <div className="App">  
   <Container className='container'>  
        <Col> 
  <CardGroup style={{'width':"auto"}}>        
  <Card className='mx-3 rounded'>  
  <Card.Img className='rounded' variant="top" src={airpod} />  
  <Card.Body>  
    <Card.Title>Oraimo FreePods3</Card.Title>  
              <Card.Subtitle className="mb-3 text-muted">$45,000</Card.Subtitle>
              <Button className='btn btn-sm btn-warning'>Add To Cart</Button><FontAwesomeIcon className='mx-3 btn btn-sm btn-warning' icon={faHeart} /> 
              </Card.Body> 
            </Card> 
<Card className='mx-3 rounded'>  
  <Card.Img className='rounded' variant="top" src={smart} />  
  <Card.Body>  
    <Card.Title>Smart Watch</Card.Title>  
              <Card.Subtitle className="mb-3 text-muted">$45,000</Card.Subtitle>
              <Button className='btn btn-sm btn-warning'>Add To Cart</Button><FontAwesomeIcon className='mx-3 btn btn-sm btn-warning' icon={faHeart} /> 
              </Card.Body> 
            </Card>  
<Card className='mx-3 rounded'>  
  <Card.Img className='rounded' variant="top" src={bluetooth} />  
  <Card.Body>  
    <Card.Title>Bluetooth Speaker</Card.Title>  
              <Card.Subtitle className="mb-3 text-muted">$45,000</Card.Subtitle>
              <Button className='btn btn-sm btn-warning'>Add To Cart</Button><FontAwesomeIcon className='mx-3 btn btn-sm btn-warning' icon={faHeart} /> 
              </Card.Body> 
            </Card>
<Card className='mx-3 rounded'>  
    <Card.Img className='rounded' variant="top" src={charger} />  
  <Card.Body>  
    <Card.Title>Oraimo Charger</Card.Title>  
              <Card.Subtitle className="mb-3 text-muted">$45,000</Card.Subtitle>
              <Button className='btn btn-sm btn-warning'>Add To Cart</Button><FontAwesomeIcon className='mx-3 btn btn-sm btn-warning' icon={faHeart} /> 
              </Card.Body> 
            </Card> 
  </CardGroup>            
    </Col>  
      </Container>  
      <br/>
    </div>  
  );  
}  
export default Products;  