import React from 'react'
import {BrowserRouter as Router} from "react-router-dom";
import { Navbar, Nav, NavDropdown } from 'react-bootstrap';
import Frame from '../assets/Frame.png'
//   import Home from './Home';
//   import AboutUs from './AboutUs';
// import ContactUs from './ContactUs';
  
function BootstrapNavbar() {
  return (
      <div>
            <div className="row">
                    <div className="col-md-12">
                  <Router>
                            <Navbar bg="light" variant="light" expand="lg" sticky="top">
                                <Navbar.Brand className="mx-4" href="#home"><img style={{'height':"30px"}} src={Frame}/></Navbar.Brand>
                                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                                <Navbar.Collapse id="basic-navbar-nav">
                                    <Nav className="mr-auto">
                                    <Nav.Link className="mx-4" href="/">Home</Nav.Link>
                                    <Nav.Link className="mx-4" href="/about-us">About Us</Nav.Link>
                                    <Nav.Link className="mx-4" href="/contact-us">Products</Nav.Link>
                                    <Nav.Link className="mx-4" href="/contact-us">Categories</Nav.Link>
                                    <Nav.Link className="mx-4" href="/contact-us">About Us</Nav.Link>
                                    <NavDropdown className="mx-4" title="Categories" id="basic-nav-dropdown">
                                        <NavDropdown.Item href="#">Phones</NavDropdown.Item>
                                        <NavDropdown.Item href="#">Fashion</NavDropdown.Item>
                                        <NavDropdown.Item href="#">Clothing</NavDropdown.Item>
                                        <NavDropdown.Item href="#">Electronics</NavDropdown.Item>
                                    </NavDropdown>
                                    </Nav>
                                    
                                </Navbar.Collapse>
                          </Navbar>
                           
                            
                        </Router>
                    </div>
                </div>

    </div>
  )
}

export default BootstrapNavbar