import React, {useState} from 'react'
import {Container, Button, Stack, Form, Tabs, Tab} from 'react-bootstrap'

function ControlledTabs() {
  const [key, setKey] = useState('home');

  return (
    <Container className="bg-light">
      <Tabs
        id="controlled-tab-example"
        activeKey={key}
        onSelect={(k) => setKey(k)}
        className="mb-5 justify-content-start fw-bold"
      >
        <Tab eventKey="home" title="All Products"></Tab>
        <Tab eventKey="Electronics" title="Electronics"></Tab>
        <Tab eventKey="Gadgets" title="Gadgets"></Tab>
        <Tab eventKey="Fashion" title="Fashion"></Tab>
        <Tab eventKey="Health" title="Health"></Tab>
        <Tab eventKey="Groceries" title="Groceries"></Tab>
        <Tab eventKey="Households" title="Households"></Tab>
        <Tab eventKey="Office" title="Office"></Tab>
        <Tab eventKey="Phones" title="Phones"></Tab>
        <Tab eventKey="Accessories" title="Accessories"></Tab>
      </Tabs>
    </Container>
  );
}

export default ControlledTabs 